.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #b2b3bc;
}

.movies {
  margin-top: 100px;
}

.movies .movie a {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  color: inherit;
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;

  /* opacity: 0.5; */
}

.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}

@media screen and (max-width: 1400px) {
  .movie {
    display: inline;
  }
}

@media screen and (max-width: 800px) {
  .movie img {
    top: 0px;
  }
  .movie {
    background-color: transparent !important;
  }
  .movie__data {
    height: 350px;
    transform: translateY(50px);
  }
  .movie__summary {
    display: none;
  }
}
