.about__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  margin-top: 100px;
  max-width: 40%;
  text-align: center;
}

.about__container span {
  font-size: 20px;
}

.about__data {
  margin-top: 50px;
  display: block;
}

.about__link i {
  font-size: 40px;
}
